<template>
    <div class="login">
      <div class="login-box">
        <img src="../assets/logo.png" class="logo">
        <!-- 表单 -->
        <el-form v-show="formflag" :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
          <el-form-item prop="username">
            <el-input v-model="ruleForm.username" prefix-icon="iconfont icon-user" ></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input type="password" v-model="ruleForm.password" prefix-icon="iconfont icon-3702mima"></el-input>
          </el-form-item>
          <el-form-item class="submit-box">
            <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
            <el-button @click="resetForm('ruleForm')" type="info">重置</el-button>
          </el-form-item>
        </el-form>
        <!-- 拼图验证码 -->
        <div v-if="flag" class="testCode" ref="ptuo">
          <!-- <p class="closeBtn"><i class="el-icon-close" @click="flag=!flag"></i></p> -->
          <slide-verify
            :l="42"
            :r="20"
            :w="450"
            :h="250"
            slider-text="向右滑动"
            @success="onSuccess"
            @fail="onFail"
            @refresh="onRefresh"
            :style="{width:'448px'}"
            class="slide-box"
            ref="slideBlock"
            v-show="flag"
          ></slide-verify>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      ruleForm: {
        username: 'admin',
        password: '123456'
      },
      rules: {
        username: [
          { required: true, message: '请输入登录名称', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '登录密码', trigger: 'change' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ]

      },
      flag: false,
      formflag: true,
      formName: ''
    }
  },
  methods: {
    submitForm (formName) {
      this.formflag = false
      this.flag = true
      this.formName = formName
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 拼图成功
    onSuccess () {
      this.flag = false
      this.$refs[this.formName].validate(async valid => {
        if (valid) {
          const res = await this.$http.post('http://47.94.247.66:8888/api/private/v1/login', this.ruleForm)
          if (res.data.data === null) {
            this.$message.error('用户名或密码错误')
            this.formflag = true
            this.flag = false
          } else {
            window.localStorage.setItem('token', res.data.data.token)
            this.$message({
              message: '登录成功',
              type: 'success'
            })
            this.$router.push('/welcome')
          }
        } else {
          return false
        }
      })
    },
    // 拼图失败
    onFail () {
      this.flag = false
      this.formflag = true
      this.$message.error('拼图失败')
    },
    // 拼图刷新
    onRefresh () {
      return true
    }

  }
}
</script>
<style lang='less'  scoped>
.login{
  width: 100%;
  height: 100%;
  background-color: #2b4b6b;
  position: relative;
  .login-box{
    width: 450px;
    height: 304px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    .logo {
      width: 130px;
      height: 130px;
      border-radius: 50%;
      border: 10px solid #fff;
      background: #ccc;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      box-shadow: 0px 0px 10px 0px #ddd;
    }
    .el-form{
      padding: 110px 25px 0px;
      .el-form-item{

      }
      .submit-box{
        text-align: right;
      }
    }
  }
}
</style>
