<template>
  <div class="home-container">
    <el-container>
      <el-header>
        <div class="left">
          <img src="../assets/heima.png" alt="" />
          <h3>电商后台管理系统</h3>
        </div>
        <el-button type="info" @click="logout">退出</el-button>
      </el-header>
      <el-container>
        <el-aside :width="!collapse?'200px':'65px'">
          <div class="collapse" @click='collapse=!collapse'>|||</div>
          <!-- 左侧菜单导航 -->
          <el-menu :default-active="defaultActive" router unique-opened class="el-menu-vertical-demo" background-color="#373d41" text-color="#fff" active-text-color="#409EFF"  @select='itenBtn' :collapse='collapse' :collapse-transition='false'>
            <el-submenu :index="item.path" v-for="(item,index) in menuList" :key="index" >
              <template slot="title">
                <i :class="classObj[item.id]"></i>
                <span>{{item.authName}}</span>
              </template>
              <el-menu-item-group v-for="(item2,index2) in item.children" :key='index2'>
                <el-menu-item :index="'/'+item2.path"><i class="el-icon-menu"></i>{{item2.authName}}</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main>
          <transition>
            <router-view></router-view>
          </transition>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      collapse: false,
      menuList: [],
      defaultActive: localStorage.getItem('defaultActive') || 'users',
      classObj: {
        125: 'iconfont icon-users',
        103: 'iconfont icon-tijikongjian',
        101: 'iconfont icon-shangpin',
        102: 'iconfont icon-danju',
        145: 'iconfont icon-baobiao'
      }
    }
  },
  // 获取左侧菜单
  created () {
    this.getMenus()
  },
  methods: {
    async getMenus () {
      const res = await this.$http.get('/menus')
      this.menuList = res.data.data
    },
    // 方法一：promise写法
    // logout () {
    //   this.$confirm('您确认要退出登录吗', '提示', {
    //     confirmButtonText: '确定',
    //     cancelButtonText: '取消',
    //     type: 'warning'
    //   }).then(() => {
    //     /* 点击的确认按钮 */
    //     localStorage.removeItem('token')
    //     this.$router.push('/login')
    //   }).catch(() => {})
    // }
    // 方法二：es7写法
    async logout () {
      const res = await this.$confirm('您确认要退出登录吗', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(() => {})
      if (res === 'confirm') {
        localStorage.removeItem('token')
        this.$router.push('/login')
      }
    },
    itenBtn (index) {
      this.defaultActive = index
      localStorage.setItem('defaultActive', index)
    }

  }
}
</script>
<style lang='less'  scoped>
.v-enter{
  transform: scale(0);
}
.v-enter-active{
 transition: all 1s;
}
.v-enter-to{
 transform:scale(1);
}
.home-container {
  width: 100%;
  height: 100%;
  .el-container {
    width: 100%;
    height: 100%;
    .el-header {
      background-color: #373d41;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left {
        width: 300px;
        display: flex;
        color: #fff;
        align-items: center;
        h3{
          margin-left: 15px;
          font-weight: normal;
        }
      }
      .el-button{
        height:40px;
      }
    }
    .el-aside {
      .collapse{
        font-size: 12px;
        text-align: center;
        color: #fff;
        cursor: pointer;
        background-color: #4a5064;
        line-height: 25px;
      }
      background-color: #373d41;
      .iconfont{
        margin-right: 8px;
      }
      .el-menu-vertical-demo{
        border:0
      }
      /deep/.el-menu-item-group__title {
        padding: 0;
      }
    }
    .el-main{
      background-color: #eaedf1;
    }
  }
}
</style>
